___CSS_LOADER_EXPORT___.push([module.id, `.link {
  display: inline-flex;
  padding: 0 10px 0 0;
  cursor: pointer;
}
.link .logo {
  width: 1em;
  height: 1em;
  filter: grayscale(1);
}
@media only screen and (max-width: 600px) {
  .link .logo {
    width: 1.25em;
    height: 1.25em;
  }
}
.link .container {
  display: flex;
  justify-content: right;
  align-items: bottom;
}
.link .subContainer {
  display: none;
  position: fixed;
  margin-top: 1.2em;
  padding-top: 0.8em;
  text-align: left;
  color: #000;
  z-index: 1;
  text-align: right;
  font-size: 17px;
  border: 1px solid transparent;
}
@media only screen and (max-width: 600px) {
  .link .subContainer {
    padding-top: 1em;
  }
}
.link .item {
  display: inline-block;
}
.link .item a {
  color: black;
  text-decoration: none;
}
@media (hover: none) {
  .link .item a {
    background-color: rgba(255, 255, 255, 0.75);
  }
}
.link .item:hover {
  color: #FFF;
}
.link .logo:hover + .subContainer {
  display: block;
}
.link .subContainer:hover {
  display: block;
}
.link .name {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  margin-top: 0.25em;
  color: rgba(0, 0, 0, 0.15);
  cursor: default;
}
