___CSS_LOADER_EXPORT___.push([module.id, `.container {
  --backgroundWhiteMan: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
.container .welcome {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  position: absolute;
}
.container .welcome .openeyes {
  position: absolute;
  margin-top: -75px;
  margin-right: -20px;
  width: min(450px, 50vw);
  height: min(150px, 20vh);
  border: 1px solid transparent;
}
.container .background {
  position: absolute;
  z-index: 0;
  width: 100vw;
  height: 100vh;
  background-image: var(--backgroundWhiteMan);
  background-color: cyan;
  background-blend-mode: lighten;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background-image 0.2s ease-in-out;
  top: 0;
  left: 0;
}
.container .background:after {
  content: '';
  position: absolute;
  display: block;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  margin-left: 5px;
  background-image: var(--backgroundWhiteMan);
  background-color: red;
  background-blend-mode: lighten;
  background-size: cover;
  mix-blend-mode: darken;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  transition: background-image 0.2s ease-in-out;
}
@keyframes stack {
  0% {
    margin-left: 0;
  }
  20% {
    margin-left: 5px;
  }
  21% {
    margin-left: -5px;
  }
  22%,
  100% {
    margin-left: 0;
  }
}
